<template>
    <div id="nav-container">
        <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
            <a class="navbar-brand" href="javascript:void(0)">
                <!--<img src="https://oj.ctguacm.work/image/ctgu.png" height="30"> {{ OJ_BRAND_NAME }}-->
            </a>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent"
                    aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <ul class="navbar-nav mr-auto">
                    <li class="nav-item">
                        <a class="nav-link" v-bind:class="{ 'active': isActive == 'index' }" href="/">{{$t('DEFAULT.OJ_HOME_PAGE')}}</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" v-bind:class="{ 'active': isActive == 'problemset' }"
                           href="/problemset.html">{{$t('DEFAULT.OJ_PROBLEMSET_PAGE')}}</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" v-bind:class="{ 'active': isActive == 'status' }" href="/status.html">{{$t('DEFAULT.OJ_STATUS_PAGE')}}</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" v-bind:class="{ 'active': isActive == 'ranklist' }" href="/ranklist.html">{{$t('DEFAULT.OJ_RANKLIST_PAGE')}}</a>
                    </li>
                </ul>
                <ul class="navbar-nav mundb-nav-right">
                    <template v-if="page_setting.is_login == false">
                        <li class="nav-item mundb-no-shrink">
                            <a class="nav-link" v-bind:class="{ 'active': isActive == 'account' }"
                               href="loginpage.html">{{$t('DEFAULT.OJ_MESSAGE_LOGIN_OR_REGISTER')}}</a>
                        </li>
                    </template>
                    <template v-else-if="page_setting.is_login == true">
                        <li class="nav-item dropdown mundb-btn-ucenter">
                            <a class="nav-link dropdown-toggle" data-toggle="dropdown" href="#" role="button"
                               aria-haspopup="true" aria-expanded="false">
                                {{page_setting.welcome}},
                                <span id="nav-username">{{user.nick}}</span>
                            </a>
                            <div class="dropdown-menu dropdown-menu-right">
                                <div class="dropdown-header">
                                    <img src="@/assets/images/user/default.png" class="mundb-avatar"
                                         id="atsast_nav_avatar">
                                    <div>
                                        <h6><span id="nav-dropdown-username">{{user.nick}}</span><br>
                                            <small>{{user.email}}</small>
                                        </h6>
                                    </div>
                                </div>
                                <div class="dropdown-divider"></div>
                                <a class="dropdown-item" href="javascript:void(0)">
                                    <i class="material-icons prefix">account_circle</i>
                                    &nbsp;&nbsp;{{$t('DEFAULT.OJ_MESSAGE_INFORMATION')}}
                                </a>
                                <a class="dropdown-item" href="javascript:void(0)">
                                    <i class="material-icons prefix">settings</i>
                                    &nbsp;&nbsp;{{$t('DEFAULT.OJ_MESSAGE_SETTINGS')}}
                                </a>
                                <a class="dropdown-item" href="/admin">
                                    <i class="material-icons prefix">face</i>
                                    &nbsp;&nbsp;{{$t('DEFAULT.OJ_MESSAGE_ADMIN')}}
                                </a>
                                <div class="dropdown-divider"></div>
                                <a class="dropdown-item" href="javascript:void(0)">
                                    <i class="material-icons prefix">group</i>
                                    &nbsp;&nbsp;{{$t('DEFAULT.OJ_MESSAGE_MY_TEAM')}}
                                </a>
                                <a class="dropdown-item" href="javascript:void(0)">
                                    <i class="material-icons prefix">insert_invitation</i>
                                    &nbsp;&nbsp;{{$t('DEFAULT.OJ_MESSAGE_MY_SUBMISSION')}}
                                </a>
                                <div class="dropdown-divider"></div>
                                <a class="dropdown-item text-danger" href="javascript:void(0)">
                                    <i class="material-icons prefix">bug_report</i>
                                    &nbsp;&nbsp;{{$t('DEFAULT.OJ_MESSAGE_MY_REPORT_BUG')}}
                                </a>
                                <a class="dropdown-item text-danger" style="cursor: pointer" @click="logout">
                                    <i class="material-icons prefix">exit_to_app</i>
                                    &nbsp;&nbsp;{{$t('DEFAULT.OJ_MESSAGE_MY_LOGOUT')}}
                                </a>
                            </div>
                        </li>
                    </template>
                    <template v-else>
                        <li class="nav-item mundb-no-shrink"></li>
                    </template>
                    <!--<div @click="test">55858</div>-->
                </ul>
            </div>
        </nav>
    </div>
</template>
<script>

    import fun from '@/assets/js/OJFunction'
    import '@/assets/js/include.js'

    export default {
        name: 'nav-bar',
        data() {
            return {
                OJ_BRAND_NAME: 'CTGUACM Online Judge',
                // isActive: "index"
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    is_login: null,
                    welcome: ""
                },
                user: {
                    email: "",
                    nick: ""
                },
                hello: ""
            }
        },
        props: ['isActive'],
        methods: {
            test: function () {
                this.hello = require('@/assets/images/user/default.png');
            },
            logout: function () {
                this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/account/logout',
                    {}, {
                        emulateJSON: true,
                        headers: {
                            OJ_ACCESS_TOKEN: this.page_setting.access_token,
                            OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                        }
                    }).then(function (res) {
                    var error = res.body.OJ_ERROR_TYPE;
                    if (error == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                        fun.delCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");
                        fun.delCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
                        location.reload();
                    }
                });
            }
        },
        created() {
            // alert(this.isActive == 'account');
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");
            // alert(this.page_setting.access_token);
            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/navbar',
                {}, {
                    emulateJSON: true,
                    headers: {
                        OJ_ACCESS_TOKEN: this.page_setting.access_token,
                        OJ_REFRESH_TOKEN: this.page_setting.refresh_token
                    }
                }).then(function (res) {
                console.log("yes1");
                var error = res.body.OJ_ERROR_TYPE;
                if (error == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                    this.user.email = res.body.OJ_EMAIL;
                    this.user.nick = res.body.OJ_NICK;
                    this.page_setting.is_login = true;
                    var myDate = new Date();
                    var h = myDate.getHours();
                    var m = myDate.getMinutes();
                    if (h >= 22 || h <= 6) this.page_setting.welcome = "Sweet dream";
                    else if (h == 7 || h == 8) this.page_setting.welcome = "Breakfast time";
                    else if ((h >= 9 && h <= 10) || (h == 11 && m <= 30)) this.page_setting.welcome = "Happy forenoon~~";
                    else if ((h == 11 && m > 30) || (h >= 12 && h <= 13)) this.page_setting.welcome = "Have lunch^_^";
                    else if ((h >= 14 && h <= 17)) this.page_setting.welcome = "Coding...";
                    else if ((h == 18)) this.page_setting.welcome = "Dinner is love";
                    else this.page_setting.welcome = "Are you a night owl";
                }else {
                    this.page_setting.is_login = false;
                }
            });
        }


    }
</script>
<style lang="scss" scoped>
    #nav-container {
        .dropdown-item {
            white-space: pre-wrap;
        }
        margin-bottom: 30px;
        position: sticky;
        top: 0;
        z-index: 899;
        flex-shrink: 0;
        flex-grow: 0;
    }
</style>